      margin:0px auto 40px auto;
    display: flex;
    flex-direction: column-reverse;
    height: 690px;
    padding-bottom: 50px;
    overflow: hidden;
    font: .85rem/1.5 Arial;
    color: #313131;
    position: relative;
  }
  
  .chat::before {
   
    content: "";
    position: absolute;
    z-index: 1;
    top: -5px;
    height: 2%;
    width: 100%;
    /* background: linear-gradient(to bottom, black 1%, rgba(255, 255, 255, 0.1)) repeat-x; */
    border-radius: 70px;
  }
  
  .chat p {
    margin: 0;
    padding: 0;
    
  }
  
  .chat__content {
    
    flex: 0 1 auto;
    padding: 1rem;
    margin: 0.5em 0.5rem;
   
    border-bottom-left-radius: 12px;
border-bottom-right-radius: 12px;
border-top-left-radius: 2px;
border-top-right-radius: 12px;
background-color: #ffffff;

  }
  
  .chat__message {
    
    width: 100%;
    display: flex;
    align-items: flex-end;
    transform-origin: 0 100%;
    padding-top: 0;
    transform: scale(0);
    max-height: 0;
    overflow: hidden;
    animation: message 0.15s ease-out 0s forwards;
    
    
    
    color: #1570e8;

    --radius: 8px 8px 8px 0;
  }
  
  .chat__message_B {
      
    flex-direction: row-reverse;
    text-align: right;
    align-self: flex-end;
    /* background-image: url('../../assets/customer.png'); */
    transform-origin: 100% 100%;
    /* --bgcolor: black; */
    --radius: 8px 8px 0 8px;
    /* border-bottom-left-radius: 12px;
border-bottom-right-radius: 12px;
border-top-left-radius: 12px;
border-top-right-radius: 2px;
background-color: #ffffff; */

  }
  .chat__message_B >p{
      position: absolute;
      bottom:-15px;
      right:55px;
      color:white;
  }
  .chat__message_A >p{
    color:white;
    position: absolute;
    bottom:-15px;
    left:55px;
  }
  .chat__message_A::before {
    content: "";
    flex: 0 0 40px;
    aspect-ratio: 1/1;
    background-image: url(data:image/png;base64,);
    border-radius: 50%;
  }
  .chat__message_B::before {
    content: "";
    flex: 0 0 40px;
    aspect-ratio: 1/1;
    background-image: url(data:image/png;base64,);
    border-radius: 50%;

  }
  
  @keyframes message {
    0% {
      max-height: 100vmax;
    }
    80% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
      max-height: 100vmax;
      overflow: visible;
      padding-top: 1rem;
    }

  }

  @media only screen and (max-width: 992px) {
    .chat {
      width: 82%;
      margin:0px 10px 40px 5px;
    display: flex;
    flex-direction: column-reverse;
      height: 450px;
    padding-bottom: 20px;
    overflow: hidden;
    font: .75rem/1.5 Arial;
    color: #313131;
    position: relative;
   
  }
  .chat__content{
    
    padding:.6em;
  }
  }
